<template>
  <!-- 幸运转转 -->
  <div class="page align-around">
    <div class="name justify-center">
      <img src="~@/assets/luckyTurn/name@2x.png" alt="" />
    </div>
    <div class="tabs justify-center">
      <div class="tab-top">
        <div
          :class="['tabi', levelActive === 1 ? 'taba' : '']"
          @click="changeLevel(1)"
        >
          9星钻/次
        </div>
        <div class="split justify-center align-center">
          <img src="../assets/luckyTurn/split@2x.png" alt="" />
        </div>
        <div
          :class="['tabi', levelActive === 2 ? 'taba' : '']"
          @click="changeLevel(2)"
        >
          99星钻/次
        </div>
        <div class="split justify-center align-center">
          <img src="../assets/luckyTurn/split@2x.png" alt="" />
        </div>
        <div
          :class="['tabi', levelActive === 3 ? 'taba' : '']"
          @click="changeLevel(3)"
        >
          999星钻/次
        </div>
      </div>
      <div class="tab-line">
        <div class="line-bg">
          <div :class="['line', levelActive === 1 ? 'line-active' : '']"></div>
          <div class="dot"></div>
          <div :class="['line', levelActive === 2 ? 'line-active' : '']"></div>
          <div class="dot"></div>
          <div :class="['line', levelActive === 3 ? 'line-active' : '']"></div>
        </div>
      </div>
    </div>
    <div class="right-btn">
      <div class="btn" @click="viewNotice">转转<br />规则</div>
      <div class="btn" @click="viewRecord">抽奖<br />记录</div>
    </div>
    <div class="content">
      <div class="turn justify-center">
        <LuckyWheel
          ref="myLucky"
          width="350px"
          height="350px"
          :prizes="prizes"
          :blocks="blocks"
          :buttons="buttons"
          @start="beforeStart"
          @end="endCallback"
        />
      </div>
      <div class="leg justify-center">
        <img src="../assets/luckyTurn/zhijia@2x.png" class="zhijia" />
      </div>
      <div class="gu">
        <img src="~@/assets/luckyTurn/gu@2x.png" />
        <div class="total">
          <div class="box">
            最高可得<span class="red">{{ maxStar }}星钻</span>
          </div>
        </div>
        <div class="tips">
          <div class="text">已有{{ joins }}人参与</div>
          <div class="text">
            您今天还有{{ `${chances_left + chances_free}` }}次机会
            <span v-show="chances_free > 0"
              >(含{{ chances_free }}次免费抽奖)</span
            >
          </div>
        </div>
      </div>
    </div>

    <Modal class="modal" :visible.sync="isShowNotice" title="抽奖规则">
      <div class="modal-content" slot="content">
        <!-- <div class="text mb-24">文案文案文案文案文案文案案文案文案文案文</div> -->
        <div ref="rule" class="sub-text mb-32 rule-lh tal" v-html="rule"></div>
      </div>
    </Modal>
    <!-- 抽奖记录 -->
    <Modal
      class="modal modal-record"
      :visible.sync="isShowRecord"
      title="抽奖记录"
    >
      <div
        class="modal-content"
        slot="content"
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="40"
      >
        <div class="item" v-for="(item, index) in recordList" :key="index">
          <div class="tit">
            <div>{{ item.title }}</div>
            <div>{{ item.cost === 0 ? "免费抽奖" : "-" + item.cost }}</div>
          </div>
          <div class="time">{{ item.create_time }}</div>
        </div>
        <!-- <divider :visible.sync="loading" :type="'text'" :title="'end'" /> -->
        <divider :visible.sync="loading" :type="'spin'" />
      </div>
    </Modal>

    <Modal class="modal" :visible.sync="isModalSpend" title="温馨提示">
      <div class="modal-content" slot="content">
        <div class="text mb-24">
          本次抽奖需要消耗{{
            levelActive == 1 ? "9" : levelActive === 2 ? "99" : "999"
          }}星钻，是否确认消费？
        </div>
      </div>
      <div class="modal-footer-row-two" slot="footer">
        <img
          @click="cancel"
          src="@/assets/images/common/modal_btn_qx.png"
          alt=""
        />
        <img
          @click="confirm2luckdraw"
          src="@/assets/images/common/modal_btn_qd.png"
          alt=""
        />
      </div>
    </Modal>

    <Modal
      class="modal"
      :visible.sync="isModalResult"
      :title="isGetPrize ? '温馨提示' : '很遗憾'"
    >
      <div class="modal-content" slot="content">
        <div class="text mb-24">
          <span v-show="isGetPrize">抽中了</span>{{ curPrize }}
        </div>
        <div v-show="isGetPrize" class="sub-text mb-32">
          奖品已自动发放至我的账户
        </div>
        <div v-show="!isGetPrize" class="text mb-24">再接再厉哦~</div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="confirmResult"
          src="@/assets/images/common/modal_again.png"
          alt=""
        />
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="cancelResult"
          src="@/assets/images/common/modal_iknow_gray.png"
          alt=""
        />
      </div>
    </Modal>

    <!-- 抽奖失败 -->
    <Modal class="modal" :visible.sync="isShowFaild" title="抽奖失败">
      <div class="modal-content" slot="content">
        <div class="text mb-24">
          {{ faildType === 1 ? "星钻不足" : "今日抽奖次数已达上限" }}
        </div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          v-if="faildType === 1"
          @click="goto('home')"
          src="@/assets/images/common/modal_btn_earn.png"
          alt=""
        />
        <img
          v-else
          @click="cancelFaild"
          src="@/assets/images/common/modal_btn_zdl.png"
          alt=""
        />
      </div>
    </Modal>

    <!-- 有免费抽奖机会 -->
    <Modal class="modal" :visible.sync="isModalFree" title="温馨提示">
      <div class="modal-content" slot="content">
        <div class="text mb-24">您有1次免费转转机会可用</div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="confirmFree2luckdraw"
          src="@/assets/images/common/modal_use.png"
        />
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="userStar2Luckdraw"
          src="@/assets/images/common/modal_have.png"
          alt=""
        />
      </div>
    </Modal>
  </div>
</template>

<script>
import Vue from 'vue'
import VueLuckyCanvas from '@lucky-canvas/vue'
Vue.use(VueLuckyCanvas)
let prizeImg = {
  starImg: [{
    src: './static/img/luckyTurn/star@2x.png',
    width: '35%',
    top: '40%'
  }],
  giftImg: [{
    src: './static/img/luckyTurn/prize@2x.png',
    width: '35%',
    top: '40%'
  }],
  goldImg: [{
    src: './static/img/luckyTurn/prize4@2x.png',
    width: '35%',
    top: '40%'
  }],

  addOneImg: [{
    src: './static/img/luckyTurn/prize3@2x.png',
    width: '35%',
    top: '40%'
  }],
  noneImg: [{
    src: './static/img/luckyTurn/prize_none@2x.png',
    width: '35%',
    top: '40%'
  }]
}

export default {
  components: {
  },
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      curStarCount: JSON.parse(localStorage.getItem('userInfo')).data.available,
      levelCount0: this.$store.getters.userInfo.levelCount0, // level0 抽奖次数
      levelCount1: this.$store.getters.userInfo.levelCount1, // level1 抽奖次数
      levelCount2: this.$store.getters.userInfo.levelCount2, // level2 抽奖次数
      levelActive: 1,
      levelList: [
        {
          title: '9星钻/次',
          id: 9
        },
        {
          title: '99星钻/次',
          id: 99
        },
        {
          title: '999星钻/次',
          id: 999
        }
      ],
      isShowNotice: false, // 抽奖规则
      isShowRecord: false, // 抽奖记录
      isModalSpend: false, // 扣除星钻提示
      isModalFree: false, // 免费抽检提示
      isModalResult: false, // 抽奖结果
      isShowFaild: false, // 抽奖失败
      faildType: 1, // 抽检失败类型，1 星钻不足， 2抽奖次数用完
      isShowSort: false,
      hasFree: true, // 是否包含免费抽奖
      curPrize: '', // 当前抽中奖品text
      isGetPrize: false, // 是否抽中奖
      blocks: [
        { padding: '10px' },
        {
          padding: '26px',
          imgs: [{
            src: './static/img/luckyTurn/lucky_bg@2x.png',
            width: '100%',
            rotate: true
          }]
        }
      ],
      prizes: [
        {
          fonts: [{ text: '+5星钻', fontSize: '14px', fontColor: '#fff', top: '15%' }],
          imgs: prizeImg.starImg
        },
        {
          fonts: [{ text: '+10星钻', fontSize: '14px', fontColor: '#fff', top: '15%' }],
          imgs: prizeImg.starImg
        },
        {
          fonts: [{ text: '+30星钻', fontSize: '14px', fontColor: '#fff', top: '15%' }],
          imgs: prizeImg.starImg
        },
        {
          fonts: [{ text: '+50星钻', fontSize: '14px', fontColor: '#fff', top: '15%' }],
          imgs: prizeImg.starImg
        },
        { fonts: [{ text: '转数+1', fontSize: '14px', fontColor: '#fff', top: '15%' }], imgs: prizeImg.addOneImg },
        { fonts: [{ text: '谢谢参与', fontSize: '14px', fontColor: '#fff', top: '15%' }], imgs: prizeImg.noneImg }
      ],
      buttons: [
        {
          radius: '50%',
          imgs: [{
            src: './static/img/luckyTurn/circle@2x.png',
            width: '100%',
            top: '-110%'
          }],
          fonts: [{ text: '点击\n开始', top: '-20px', fontColor: '#fff' }]
        }
      ],
      maxStar: 0, // 当前抽奖级别最高可获取的星钻数
      joins: 0, // 当前抽奖级别 已有参与人数
      chances_left: 0, // 当前抽奖级别 剩余抽奖次数
      chances_free: 0, // 当前抽奖级别 剩余免费抽奖次数
      use_free: '0', // 1使用免费抽奖机会，0 不适用免费机会

      recordList: [
        {
          title: '100星钻',
          cost: '9',
          time: '2022-03-05 13:09:23',
          id: 1
        },
        {
          title: '谢谢参与',
          cost: '9',
          time: '2022-03-05 13:09:23',
          id: 7
        },
        {
          title: '+1抽奖机会',
          cost: '9',
          time: '2022-03-05 13:09:23',
          id: 6
        }

      ],
      page_num: 1,
      page_size: 10,
      loading: false,
      is_last_page: false, // 是否抽奖记录最后一页
      rule: '' // 转转规则
    }
  },
  created () {
    this.recordList = []
    this.getLuckySets(this.levelActive)
    this.getCharges(this.levelActive)
  },
  methods: {
    changeLevel (level) {
      this.levelActive = level
      console.log(level)
      // if (level === 0) {
      //   this.prizes = this.prizes1
      // } else if (level === 1) {
      //   this.prizes = this.prizes2
      // } else {
      //   this.prizes = this.prizes3
      // }
      this.maxStar = ''
      this.getLuckySets(level)
      this.getCharges(level)
    },
    goList () {
      this.$router.push('invitationList')
    },
    goto (name) {
      if (name === 'home') {
        this.$router.push({ name: name, params: { earnStar: '1' } })
      }
    },
    // 点击抽奖按钮会触发star回调
    // 先判断，后抽奖
    beforeStart () {
      if (this.chances_free > 0) {
        this.isModalFree = true
        this.use_free = 0
      } else {
        this.isModalSpend = true
      }
    },
    // 抽奖
    async doLottery () {
      // 重置抽奖信息
      this.curPrize = ''
      this.isGetPrize = false
      console.log(this.$store.getters.userInfo)

      // 调用抽奖组件的play方法开始游戏
      this.$refs.myLucky.play()
      let res = await this.$request('get', 'user/dolottery', {
        type: this.levelActive,
        uid: this.userInfo.uid,
        use_free: this.use_free // 是否使用免费抽奖机会1
      })
      if (res && res.error_no === '0' && res.data) {
        console.log('api返回抽奖结果：res.data')
        console.log(res.data)
        if (res.data.type === '2') {
          this.isGetPrize = false
        } else {
          this.isGetPrize = true
        }

        setTimeout(() => {
          // 调用stop停止旋转并传递中奖索引
          this.$refs.myLucky.stop(res.data.id - 1 || 5)
        }, 2000)
      }
    },
    // 确认花费星钻抽奖
    confirm2luckdraw () {
      this.isModalSpend = false
      this.use_free = '0'
      // 星钻是否够
      if ((this.levelActive === 1 && this.curStarCount < 9) ||
        (this.levelActive === 2 && this.curStarCount < 99) ||
        (this.levelActive === 3 && this.curStarCount < 999)
      ) {
        this.faildType = 1
        this.isShowFaild = true
      } else if (this.chances_free < 1 && this.chances_left < 1) {
        // 抽奖次数是否够
        this.faildType = 2
        this.isShowFaild = true
      } else {
        this.doLottery()
      }
    },
    // 确认免费抽奖
    confirmFree2luckdraw () {
      this.isModalFree = false
      this.use_free = '1'
      this.doLottery()
    },
    // 花费cancel
    cancel () {
      this.isModalSpend = false
    },
    // 抽奖结果cancel
    cancelResult () {
      this.isModalResult = false
    },
    // 抽奖失败cnacel
    cancelFaild () {
      this.isShowFaild = false
    },
    confirmResult () {
      this.cancelResult()
      this.confirm2luckdraw()
    },
    // 我有星钻，使用星钻抽检
    userStar2Luckdraw () {
      this.isModalFree = false

      this.use_free = '0'
      if (this.chances_left === 0) {
        this.faildType = 2
        this.isShowFaild = true
      } else {
        this.faildType = 1
        this.isModalSpend = true
      }
    },
    // 抽奖结束会触发end回调
    endCallback (prize) {
      console.log('奖品配置中奖结果：')
      console.log(prize)
      let curp = prize.fonts[0].text
      if (curp.startsWith('+')) { // 星钻类奖励
        this.curPrize = curp.slice(1)
      } else {
        this.curPrize = curp
      }
      // 再次获取星钻，抽奖机会
      this.getCharges(this.levelActive)
      this.getAccount()
      this.isModalResult = true
    },
    viewNotice () {
      this.getRule()
      // this.isShowNotice = true
    },
    async viewRecord () {
      this.isShowRecord = true
      this.page_num = 1
      this.getRecord()
    },
    // 获取抽奖记录
    async getRecord () {
      // this.isShowRecord = true
      // 获取抽奖记录
      let res = await this.$request('get', 'user/lotterypagelist', {
        page_num: this.page_num,
        page_size: this.page_size,
        uid: this.userInfo.uid
      })
      if (res && res.error_no === '0') {
        this.loading = false
        if (this.page_num === 1) {
          this.recordList = res.data.list
        } else {
          this.recordList = this.recordList.concat(res.data.list)
        }
        if (this.page_num * this.page_size >= res.data.total) {
          this.is_last_page = true
        } else {
          this.is_last_page = false
        }
      }
    },
    // 抽奖记录加载更多
    loadMore () {
      if (!this.is_last_page) {
        this.loading = true
        this.page_num += 1
        this.getRecord()
      }
      // setTimeout(() => {
      //   for (let i = 1; i <= 10; i++) {
      //     let last = {
      //       title: '100星钻',
      //       cost: '9',
      //       time: '2022-03-05 13:09:23'
      //     }
      //     this.recordList.push(last)
      //   }
      //   this.loading = false
      // }, 1500)
    },
    // 转转规则
    async getRule () {
      let res = await this.$request('get', 'activity/getrule', { type: 2 })
      if (res && res.error_no === '0') {
        this.rule = res.data
        // this.rule = `<p>
        //   1、 加好友
        //   这是最基本的方法了。加的人多，能偷的能量也多，不过相应的别人也可以偷你自己的能量。所以能做的就是，早上不能睡懒觉。为了种树，勤劳是第一位的。
        // </p>
        // <p class="pcenter">
        //   2、 线下支付
        //   选择有支付宝字样的商家进行线下支付，一次支付行为可以获得5g的能量。需要注意一定是有支付宝字样支付的才可以，线下扫支付宝二维码转账给个人的那种支付方式，不能获得能量。
        // </p>
        // <p>
        //   3、 生活缴费
        //   缴水费、电费、燃气费，同账户同户号每月缴费只能产生1次能量，同账户不同户号缴费每月最多能量产生10次，金额没有限制，一元即可。但是在具体的能量数值方面，一般是285g，但也有人品大爆发的可以获得915g能量的。
        // </p>`
        this.isShowNotice = true
      }
    },
    // 获取抽奖配置信息
    async getLuckySets (type) {
      // this.isShowRecord = true
      // 获取抽奖记录
      let res = await this.$request('get', 'activity/getlotterylist', {
        type: type
      })
      if (res && res.error_no === '0' && res.data && res.data.length) {
        // console.log(res)
        res.data.forEach((ele, index) => {
          let ptext, img

          // 奖励类型，1-星钻奖励，2-谢谢参与，3-抽奖次数，4-AI体验
          if (ele.type === '1') {
            ptext = '+' + ele.title
            img = prizeImg.starImg
            this.maxStar = Math.max(this.maxStar, ele.worth)
          } else if (ele.type === '2') {
            ptext = ele.title
            img = prizeImg.noneImg
          } else if (ele.type === '3') {
            ptext = ele.title
            // ptext = '转数+1次'
            img = prizeImg.addOneImg
          } else if (ele.type === '4') {
            ptext = ele.title
            img = prizeImg.goldImg
          }
          this.prizes[index].fonts[0].text = ptext
          this.prizes[index].imgs = img
        })
      }
    },
    // 剩余抽奖次数查询
    async getCharges (type) {
      // this.isShowRecord = true
      // 获取抽奖记录
      let res = await this.$request('get', 'user/getlotteryinfo', {
        type: type,
        uid: this.userInfo.uid
      })
      if (res && res.error_no === '0' && res.data) {
        this.chances_free = res.data.chances_free
        this.chances_left = res.data.chances_left
        this.joins = res.data.joins
      }
    },
    // 获取用户当前星钻
    async getAccount (type) {
      let res = await this.$request('get', 'user/getaccount', {
        uid: this.userInfo.uid
      })
      if (res && res.error_no === '0' && res.data) {
        this.$store.commit('setUserInfo', res.data)
      }
    }
  }
}
</script>

