        img {
          width: 12px;
          height: 21px;
        }
      }
    }
    .tab-line {
      height: 12px;
      // width: calc(100% - 74px);
      padding: 0 37px;
      width: 100%;
      margin-top: 7px;
      .line-bg {
        width: 100%;
        height: 100%;
        .bg-image("../assets/luckyTurn/line_bg","png");
        .flex_func(row, space-between,center,nowrap);
        .line {
          // width: calc(33.33% - 8px);
          width: 33.33%;
          height: 100%;
          // background: greenyellow;
        }
        .line-active {
          .bg-image("../assets/luckyTurn/line_active","png");
        }
        .dot {
          width: 12px;
          height: 12px;
          // margin: 0 -6px;
          .bg-image("../assets/luckyTurn/dot","png");
        }
      }
    }
  }
  .right-btn {
    width: 78px;
    height: 176px;
    position: fixed;
    right: 0;
    top: 310px;
    z-index: 3;
    .btn {
      width: 78px;
      height: 78px;
      margin-bottom: 10px;
      padding: 9px 0;
      .bg-image("~@/assets/luckyTurn/btn_bg","png");
      font-size: 22px;
      text-align: center;
      line-height: 30px;
      font-weight: 500;
      color: #fffbe9;
    }
  }
  .content {
    width: 100%;
    height: calc(100% - 202px);
    position: fixed;
    bottom: 0;
    .flex_func(column,flex-end, center, nowrap);
    .turn {
      width: 100%;
      height: 550px;
      margin-bottom: -130px;
      position: relative;
      z-index: 2;
    }
    .leg {
      width: 100%;
      height: 245px;
      margin-bottom: -40px;
      position: relative;
      > img {
        width: 430px;
        height: 245px;
      }
    }
    .total {
      width: 100%;
      height: 45px;
      line-height: 45px;
      text-align: center;
      position: absolute;
      z-index: 2;
      bottom: 250px;
      .box {
        width: 300px;
        height: 45px;
        margin: 0 auto;
        background: #fffcff;
        border-radius: 23px;
        font-size: 30px;
        font-family: Source Han Sans SC;
        font-weight: 500;
        color: #924336;
        .red {
          color: #e83f66;
        }
      }
    }
    .tips {
      position: absolute;
      z-index: 2;
      bottom: 65px;
      width: 100%;
      height: 90px;
      text-align: center;
      font-size: 30px;
      font-family: Source Han Sans SC;
      font-weight: 500;
      color: #ffffff;
      .text {
        line-height: 45px;
      }
    }
  }
}
.modal-record {
  .modal-content {
    // padding: 0 45px 80px;
    min-height: 600px;
    // background: pink;
    .item {
      width: 100%;
      height: 155px;
      margin-bottom: 28px;
      .bg-image("~@/assets/luckyTurn/bg_record","png");
      padding: 0 25px;
      .flex_func(column,center,center,nowrap);
      .tit {
        height: 90px;
        width: 100%;
        font-size: 32px;
        font-weight: 500;
        color: #333333;
        line-height: 45px;
        .flex_func(row,space-between,center,nowrap);
      }
      .time {
        width: 100%;
        height: 65px;
        line-height: 65px;
        text-align: left;
        font-size: 24px;
        font-weight: 400;
        color: #999999;
        line-height: 33px;
      }
    }
  }
}
</style>



